import { defineComponent } from "vue";
import { startApplet, appletList } from "@front/util_function/system";
export default defineComponent({
	data() {
		return {
			applets: []
		};
	},
	mounted() {
		this.refreshList();
	},
	methods: {
		startApplet,
		refreshList() {
			appletList().then(res => {
				(this.applets as any) = res;
			});
		}
	}
});
